let UIBase = require("UIBase");
let UIManager = require("UIManager");

let VUI = cc.Class({
    extends: UIBase,

    properties: {
        microButton: cc.Node,
        recordButton: cc.Node,

        stream: null,
        recorder: null,

        //
        useWebRecognition: true,
        recognition: null,

        //api

        gumStream : null,
        //stream from getUserMedia() 
        rec : null,
        //Recorder.js object 
        input : null,
        audioContext:null,
       
    },

    //mobile
    startRecord() {
        this.microButton.active = false;
        this.recordButton.active = true;
        this.textRecognition = "";

        this.text.enabled = false;

        window.SpeechRecognition = window.SpeechRecognition
            || window.webkitSpeechRecognition;

        this.useWebRecognition = SpeechRecognition != undefined;

        UIManager.instance.debugNode.string += "useWebRecognition:" +  this.useWebRecognition + "\n";
        
        if(this.useWebRecognition){
            this.startWebRecognition();
        }
        else{
            this.startAPIRecognition();
        }
        
    },

    stopRecord() {
        this.microButton.active = true;
        this.recordButton.active = false;


        if(this.useWebRecognition){
           this.stopWebRecognition();
        }
        else{
            this.stopAPIRecognition();
        }
       

        this.text.enabled = true;
        this.updateUIAfterRecord();
    },

    startWebRecognition(){
        this.recognition = new SpeechRecognition();
        this.recognition.interimResults = true;
        this.recognition.continuous = true;

        let that = this;
        let finalString = "";
    
        this.recognition.onresult = function (event) {

            let interString = "";
            for (let i = event.resultIndex; i < event.results.length; ++i) {
                // If the result item is Final, add it to Final Transcript, Else add it to Interim transcript
                if (event.results[i].isFinal) {
                    finalString += event.results[i][0].transcript;
                    that.textRecognition = finalString;
                   
                }
                else{
                    interString += event.results[i][0].transcript;
                    that.textRecognition = interString;
                   
                }
            }
            that.text.string = that.textRecognition;

            if (that.text.enabled) {
                that.updateUIAfterRecord();
            }
        };

        this.recognition.start();
        //this.recognition.addEventListener('end',  this.recognition.start);
    },

    stopWebRecognition(){
        this.recognition.stop();
    },

    
    //start api recognition
    startAPIRecognition(){
        
        var AudioContext = window.AudioContext || window.webkitAudioContext;
        
        //new audio context to help us record 

        let that = this;

        let constraints  =  {
            audio: true,
            video: false
        } ;

        navigator.mediaDevices.getUserMedia(constraints).then(function(stream) {

            that.audioContext = new AudioContext();

            console.log("getUserMedia() success, stream created, initializing Recorder.js ..."); 
            /* assign to gumStream for later use */
            that.gumStream = stream;
            /* use the stream */
            that.input = that.audioContext.createMediaStreamSource(stream);
            /* Create the Recorder object and configure to record mono sound (1 channel) Recording 2 channels will double the file size */
            that.rec = new Recorder(that.input, {
                numChannels: 1
            }) 
            //start the recording process 
            that.rec.record()
            console.log("Recording started");
        }).catch(function(err) {
            
        });
    },

    stopAPIRecognition(){
        this.rec.stop(); //stop microphone access 
        this.gumStream.getAudioTracks()[0].stop();
        //create the wav blob and pass it on to createDownloadLink 
        this.rec.exportWAV(function(blob) {


            let xhr = new XMLHttpRequest();
            let requestURL = UIManager.instance.game.URL + "/audio-to-text/" + UIManager.instance.game.URL_SUFFIX;
            xhr.onreadystatechange = function () {
                if (xhr.readyState == XMLHttpRequest.DONE) {
                    if (xhr.status == 200) {
                       console.log(xhr.responseText);
                    }
                }
            }
            //let params = JSON.stringify({ "audio": blob});
            xhr.open('POST', requestURL, true);

            var fd=new FormData();
            fd.append("audio",blob);

            xhr.send(fd);
          });

       
    },

    createDownloadLink(blob) {
        let URL = window.URL || window.webkitURL;

        console.log('createDownloadLink',blob);
    },

    updateUIAfterRecord() {
        this.sendButton.active = (this.text.string.length != 0);
        this.updateSendButton(this.sendButton.active);
    },

    updateSendButton(active) {
        this.microButton.active = !active;
    }

});